import { LOGIN_USER_SUCCESS, REFRESH_USER_SUCCESS } from "./loginUserType";
import { postHttp } from "../../common/HttpService";


export const loginUserSuccess = (user) => {
  return {
    type: LOGIN_USER_SUCCESS,
    payload: user,
  };
};

export const refreshUserSuccess = (user) => {
  return {
    type: REFRESH_USER_SUCCESS,
    payload: user,
  };
};



export const loginUser = (accountNo,password) => {
    return (dispatch) => {
        // dispatch(fetchUserRequest);
        const windowUrl = window.location.search;
        const params = new URLSearchParams(windowUrl);
    
      
        console.log(' Login User');
        const remoteUrl = `/api/login`;
        postHttp({ url: remoteUrl,body:{accountNo:accountNo,password:password}}, false)
            .then(response => {
                // const users = response.data;
                let user = { access_token: response.token, refresh_token: response.validity,user_type:response.userType};
               dispatch(loginUserSuccess(user));
              
                
            })
            .catch(error => {
                // const errMsg = error.message;
                // dispatch(fetchUserFailure(errMsg));
            })
    }
}

export const loginWithAuthData = (obj) => {
  return (dispatch) => {
    let user = {
      access_token: obj.access_token,
      refresh_token: obj.refresh_token,
    };
    dispatch(loginUserSuccess(user));
  };
};
